import { mapState } from "vuex";
import uuidv1 from "uuid/v1";
import { date } from "quasar";

export default {
  name: "App",

  components: {},

  data() {
    return {
      githubData: null,
      left: true,
      mini: false,
      link: "now",
      textshow: false,
      colorPickerShow: false,
      title: "",
      pickColor: "white",
      text: "",
      searchText: "",
      colors: [
        "indigo-2",
        "indigo-11",
        "purple-2",
        "grey",
        "cyan-1",
        "teal-1",
        "green-1",
        "yellow-1"
      ]
    };
  },
  methods: {
    showMenu() {
      if (this.left) {
        this.mini = !this.mini;
      } else {
        this.left = !this.left;
      }
    },
    clearAll() {
      this.textshow = false;
      this.pickColor = "white";
      this.text = "";
      this.title = "";
    },
    addNote() {
      const note = {
        title: this.title,
        text: (this.text + "").replace(/\n/g, "<br/>"),
        id: uuidv1(),
        state: "running",
        active: false,
        color: this.pickColor,
        time: date.formatDate(new Date(), "YYYY-MM-DD HH:mm:ss")
      };
      this.todoList.push(note);
      this.$store.dispatch("saveTask", {
        todoList: this.todoList
      });
      this.clearAll();
    }
  },
  computed: {
    ...mapState({
      todoList: state => {
        return state.todoList;
      },
      postState: state => {
        return state.postState;
      }
    })
  },
  mounted() {
    if (this.todoList.length == 0) {
      this.$store.dispatch("init");
    }
  }
};
